import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Swal from 'sweetalert2';

import editIcon from '../../assets/editIcon.svg';
import deleteIcon from '../../assets/deleteIcon.svg';
import viewIcon from '../../assets/viewIcon.svg';
import { getTotalPages } from '../../helpers/getTotalPages';
import { onView, uiOpenModal } from '../../actions/ui';
import { actionModalClient, clearSearchingClients, startActiveClient, startDeleteClient } from '../../actions/clients';
import { clearSearchingProducts, startActiveProduct, startDeleteProduct } from '../../actions/products';

const ITEMS_PER_PAGE = 5;

const DataTable = ( props ) => {

    const dispatch = useDispatch();
    
    const totalPages = getTotalPages(props.items, ITEMS_PER_PAGE);
    
    const { clientList, clientsFind } = useSelector( state => state.client );
    const { productList, productFind } = useSelector( state => state.product );
    const { view } = useSelector( state => state.ui );

    const dataFromApi = props.items;
    const [ items, setItems ] = useState( [...props.items].splice( 0, ITEMS_PER_PAGE ) );
    const [currentPage, setCurrentPage] = useState(0);

    useEffect(() => {
        dispatch( onView( localStorage.getItem('thisPage') ) );
        dispatch( clearSearchingClients() );
        dispatch( clearSearchingProducts() );
    }, [dispatch]);

    useEffect(() => {
        if( view === 'clientes' && clientsFind ){
            setItems([...clientsFind].splice( 0, ITEMS_PER_PAGE ));
            setCurrentPage(0);
        }else if( view === 'clientes' ){
            setItems([...clientList].splice( 0, ITEMS_PER_PAGE ));
            setCurrentPage(0);
        }else if( view === 'articulos' && productFind ){
            setItems([...productFind].splice( 0, ITEMS_PER_PAGE ));
            setCurrentPage(0);
        }else if( view === 'articulos' ){
            setItems([...productList].splice( 0, ITEMS_PER_PAGE ));
            setCurrentPage(0);
        }
    }, [clientsFind, clientList, productList, productFind, view]);
    
    const scrollToTop = () => {
        document.getElementById('table').scrollIntoView();
    }

    const nextPage = () => {
        if( totalPages > 0 ){
            const nextPage = currentPage + 1;
    
            if(nextPage !== totalPages){
                const firstIndex = nextPage * ITEMS_PER_PAGE;
                setCurrentPage(nextPage);
                setItems( [...dataFromApi].splice( firstIndex, ITEMS_PER_PAGE ) );
                scrollToTop();
            }
        }
    }

    const prevPage = () => {
        if( totalPages > 0 ){
            const prevPage = currentPage - 1;
            
            if( prevPage >= 0 ){
                const firstIndex =  prevPage * ITEMS_PER_PAGE;
    
                setItems([...dataFromApi].splice(firstIndex, ITEMS_PER_PAGE))
                setCurrentPage(prevPage);
                scrollToTop();
            }
        }
    }

    const hadleAction = ( fieldCode, action, bodega ) => {
        //dispatch para saber la action que se va a realizar
        dispatch( actionModalClient( action ) );

        //dispatch para crear un cliente activo en el modal
        if( view === 'clientes' )
            dispatch( startActiveClient( fieldCode, dataFromApi ) );

        //dispatch para crear un cliente activo en el modal
        if( view === 'articulos' )
            dispatch( startActiveProduct( fieldCode, dataFromApi, bodega ) );
        
        dispatch( uiOpenModal() );
    }

    const hadleDelete = ( fieldCode, bodega ) => {
        if( view === 'clientes' ){
            Swal.fire({
                title: '¿Desea eliminar el cliente?',
                showDenyButton: true,
                confirmButtonText: 'Eliminar',
                confirmButtonColor: '#0191CE',
                denyButtonText: `No eliminar`,
            }).then(( result ) => {
                if( result.isConfirmed ){
                    dispatch( startDeleteClient( fieldCode ) );
                }
            })
        }

        if( view === 'articulos' ){
            Swal.fire({
                title: '¿Desea eliminar el artículo?',
                showDenyButton: true,
                confirmButtonText: 'Eliminar',
                confirmButtonColor: '#0191CE',
                denyButtonText: `No eliminar`,
            }).then(( result ) => {
                if( result.isConfirmed ){
                    dispatch( startDeleteProduct( fieldCode.trim(), bodega.trim() ) );
                }
            })
        }
    }


    if( view === 'clientes' ){
        return (
            <div>
                <div className='table-client'>
                    <table id='table'>
                        <thead>
                            <tr>
                                <th>Código</th>
                                <th>RUC</th>
                                <th>Nombre</th>
                                <th>Editar</th>
                                <th>Eliminar</th>
                                <th>Ver</th>
                            </tr>
                        </thead>

                        <tbody>
                            {
                                items.length > 0
                                    ? items.map( ( item, index ) => {
                                        return (
                                            <tr key={index}>
                                                <td data-label='Código'>{item.codigoc}</td>
                                                <td data-label='RUC'>{item.ruc}</td>
                                                <td data-label='Nombre'>{item.nombrec}</td>
                                                <td data-label='Editar'>
                                                    <div className='container-actions'>
                                                        <div
                                                            onClick={ () => hadleAction( item.codigoc, 'edit' ) }
                                                            className='container-actions-1'
                                                        >
                                                            <img src={editIcon} alt='edit' />
                                                        </div>
                                                    </div>
                                                </td>
                                                <td data-label='Eliminar'>
                                                    <div className='container-actions'>
                                                        <div 
                                                            className='container-actions-2'
                                                            onClick={ () => hadleDelete( item.codigoc ) }
                                                        >
                                                            <img src={deleteIcon} alt='delete' />
                                                        </div>
                                                    </div>
                                                </td>
                                                <td data-label='Ver'>
                                                    <div className='container-actions'>
                                                        <div 
                                                            className='container-actions-3'
                                                            onClick={ () => hadleAction( item.codigoc, 'view' ) }
                                                        >
                                                            <img src={viewIcon} alt='view' />
                                                        </div>
                                                    </div>
                                                </td>
                                            </tr>
                                        )
                                    })

                                    :   <tr>
                                            <td data-label='Código'>-</td>
                                            <td data-label='RUC'>-</td>
                                            <td data-label='Nombre'>-</td>
                                            <td data-label='Editar'>-</td>
                                            <td data-label='Eliminar'>-</td>
                                            <td data-label='Ver'>-</td>
                                        </tr>
                            }
                        </tbody>
                    </table>
                </div>

                <div className='pagination'>
                    <div className='pagination-1'>
                        <button 
                            className='pagination-button'
                            onClick={ prevPage }
                        >
                            <i className="fa fa-angle-left"></i>
                        </button>
                        <div className='pagination-num'>
                            <p className='current-page'>{ totalPages > 0 ? currentPage + 1 : currentPage }</p>
                            <p className='total-pages'>/ { totalPages }</p>
                        </div>
                        <button
                            className='pagination-button'
                            onClick={ nextPage }
                        >
                            <i className="fa fa-angle-right"></i>
                        </button>
                    </div>
                </div>
            </div>
            
        )
    }else if( view === 'articulos' ){
        return (
            <div>
                <div className='table-product'>
                    <table id='table'>
                        <thead>
                            <tr>
                                <th>Bodega</th>
                                <th>Código</th>
                                <th>Nombre</th>
                                <th>Precio</th>
                                <th>Editar</th>
                                <th>Eliminar</th>
                                <th>Ver</th>
                            </tr>
                        </thead>

                        <tbody>
                            {
                                items.length > 0
                                    ? items.map( ( item, index ) => {
                                        return (
                                            <tr key={index}>
                                                <td data-label='Bodega'>{item.bodega}</td>
                                                <td data-label='Código'>{item.codigoa}</td>
                                                <td data-label='Nombre'>{item.nombrea}</td>
                                                <td data-label='Precio'>{item.precio_1}</td>
                                                <td data-label='Editar'>
                                                    <div className='container-actions-product'>
                                                        <div
                                                            className='container-actions-1-product'
                                                            onClick={ () => hadleAction( item.codigoa, 'edit', item.bodega ) }
                                                        >
                                                            <img src={editIcon} alt='edit' />
                                                        </div>
                                                    </div>
                                                </td>
                                                <td data-label='Eliminar'>
                                                    <div className='container-actions-product'>
                                                        <div 
                                                            className='container-actions-2-product'
                                                            onClick={ () => hadleDelete( item.codigoa, item.bodega ) }
                                                        >
                                                            <img src={deleteIcon} alt='delete' />
                                                        </div>
                                                    </div>
                                                </td>
                                                <td data-label='Ver'>
                                                    <div className='container-actions-product'>
                                                        <div 
                                                            className='container-actions-3-product'
                                                            onClick={ () => hadleAction( item.codigoa, 'view', item.bodega ) }
                                                        >
                                                            <img src={viewIcon} alt='view' />
                                                        </div>
                                                    </div>
                                                </td>
                                            </tr>
                                        )
                                    })

                                    :   <tr>
                                            <td data-label='Código'>-</td>
                                            <td data-label='Nombre'>-</td>
                                            <td data-label='Precio'>-</td>
                                            <td data-label='Editar'>-</td>
                                            <td data-label='Eliminar'>-</td>
                                            <td data-label='Ver'>-</td>
                                        </tr>
                            }
                        </tbody>
                    </table>
                </div>

                <div className='pagination-product'>
                    <div className='pagination-1-product'>
                        <button 
                            className='pagination-button-product'
                            onClick={ prevPage }
                        >
                            <i className="fa fa-angle-left"></i>
                        </button>
                        <div className='pagination-num-product'>
                            <p className='current-page-product'>{ totalPages > 0 ? currentPage + 1 : currentPage }</p>
                            <p className='total-pages-product'>/ { totalPages }</p>
                        </div>
                        <button
                            className='pagination-button-product'
                            onClick={ nextPage }
                        >
                            <i className="fa fa-angle-right"></i>
                        </button>
                    </div>
                </div>
            </div>
        )
    }else{
        return (
            <div>
                <p>...</p>
            </div>
        )
    } 
}

export default DataTable;